
.cartItems {
    list-style: none;
    margin: 0;
    padding: 0;
    max-height: 20rem;
    overflow: auto;
  }
  
  .total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 1.5rem;
    margin: 1rem 0;
  }
  
  .actions {
    text-align: right;
  }
  
  .actions button {
    font: inherit;
    cursor: pointer;
    background-color: transparent;
    border: 1px solid #3B3355;
    padding: 0.5rem 2rem;
    border-radius: 25px;
    margin-left: 1rem;
    margin-bottom: 4px;
  }
  
  .actions button:hover,
  .actions button:active {
    background-color: #5D5D81;
    border-color: #5D5D81;
    color: #FEFCFD;
  }
  
  .actions .closeButton {
    color: #3B3355;
  }
  
  .actions .button {
    background-color: #3B3355;
    color: #FEFCFD;
  }